___CSS_LOADER_EXPORT___.push([module.id, `figure {
    width: 10rem;
    height: 10rem;
    min-width: 10rem;
    min-height: 10rem;
    aspect-ratio: 1 / 1;
    margin-right: 1rem;

    border-radius: 50%;
    border: .2rem solid rgba(229, 229, 229, 1);
    overflow: hidden;

    & img {
        width: 100%;
        height: 100%;
    }
}

.main {
    display: flex;
    flex-direction: row;
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.nome {
    font-weight: 400;
    font-size: 2rem;
}

.profileLink {
    transition: color .25s;
}

.profileLink:hover {
    color: #fff;
    cursor: pointer;
    transition: all .5s;
}

.arroba, .desc {
    font-weight: 400;
    font-size: 1rem;
}

.arroba {
    vertical-align: top;
}

@media (max-width: 390px) {
    .main {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
    }
