import './styles.css';

const ItemList = ({repos}) => {
  return (
    <>
        {repos?.length ? (
        <>
            <h1 className='ItemListH1'>Repositrios</h1>
            <ul>
                {repos.map((repo) => (
                    <li>
                        <div className="title">
                            <a className="repoLink" target='_blank' href={repo.html_url}>
                                {repo.name}
                            </a>
                        </div>
                        <div className="disc">{repo.description || "Sem descrio"}</div>
                        <hr />
                    </li>
                ))}
            </ul>
        </>
        ):(
            <p>Nenhum repositrio encontrado.</p> // Mensagem para quando no h repositrios
        )}
    </>
  )
}

export { ItemList };
